<template>
    <section class="pc-container pb-4 px-4">
        <div class="pcoded-content">
            <div class="card">
                <div class="card-header py-3">
                    <div class="d-flex align-items-center">
                        <h5>Lista de Especialidades</h5>
                        <button class="btn btn-success btn-sm px-4 ml-auto rounded-1" @click="mostrarModal">
                            Nuevo
                        </button>
                    </div>
                </div>
                <div class="card-body">
                    <div class="table-responsive">
                        <table class="table table-bordered" id="miTabla1">
                            <thead>
                                <tr>
                                    <th class="text-center pl-4 col-lg-2 col-sm-1">#</th>
                                    <th>Nombre</th>
                                    <th class="text-center">Opciones</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(especialidad, index) in especialidades" :key="especialidad.esp_id">
                                    <td class="text-center py-2 my-0">
                                        <input class="form-check-input me-1" type="checkbox" value="" />
                                    </td>
                                    <td>{{ especialidad.esp_nombre }}</td>
                                    <td class="text-center">
                                        <a href="javascript:void(0)" @click="editar(especialidad.esp_id)"
                                            class="text-primary px-2 py-1 btn-lg mx-1">
                                            <span class="">
                                                <i data-feather="edit"></i>
                                            </span>
                                        </a>
                                        <a href="javascript:void(0)" @click="borrar(especialidad.esp_id)"
                                            class="px-2 py-1 btn-lg text-danger">
                                            <span>
                                                <i data-feather="trash"></i>
                                            </span>
                                        </a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div v-if="showModal" class="modal" @click="cerrarModal">
                <div class="modal-content card border-0" @click.stop>
                    <div class="card-header">
                        <h5 class="text-center">
                            {{ especialidad.esp_id ? 'Actualizar especialidad' : 'Registar Nueva especialidad' }}
                        </h5>
                    </div>
                    <div class="card-body mb-0 pb-0">
                        <div class="row">
                            <div class="col-md-12">
                                <div class="form-group">
                                    <label class="form-label">Nombre</label>
                                    <input class="form-control" v-model="especialidad.esp_nombre" @blur="validarNombre"
                                        @input="errores.esp_nombre = false" @keyup.enter="guardar"
                                        :class="{ 'has-error': errores.esp_nombre }">
                                    <span class="error-message" v-if="errores.esp_nombre">Ingrese un nombre de
                                        Especialidad.</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="card-footer border-0 mt-0 pt-0">
                        <div class="d-flex justify-content-end">
                            <button class="btn btn-secondary btn-lg  px-4 mx-2 rounded" @click="cerrarModal">Cerrar</button>
                            <button class="btn btn-primary btn-lg  px-4 rounded" @click="guardar">
                                {{ especialidad.esp_id ? 'Actualizar' : 'Guardar' }}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import feather from 'feather-icons';
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import { mapActions } from 'vuex';

export default {
    name: "djudiciales",
    created() {
        this.leer();
    },

    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            dataTable: null,
            showModal: false,
            especialidades: [],
            especialidad: {
                esp_id: "",
                esp_nombre: "",
            },
            errores: {
                esp_nombre: false
            }
        };
    },

    methods: {
        ...mapActions(["get", "post"]),

        validarCampo(campo, propiedadError) {
            if (this.especialidad[campo] === "") {
                this.errores[propiedadError] = true;
            } else {
                this.errores[propiedadError] = false;
            }
        },

        validarNombre() {
            this.especialidad.esp_nombre = this.especialidad.esp_nombre;
            this.validarCampo('esp_nombre', 'esp_nombre');
        },

        validarCampos() {
            this.validarNombre();
            return !Object.values(this.errores).some(error => error);
        },

        async leer() {
            try {
                const responseEspecialidades = await this.get({
                    url: this.$store.getters.get__url + "/specialty",
                    token: this.$store.getters.get__token
                });

                this.especialidades = responseEspecialidades.data;
                this.initDataTable();
            } catch (errorleer) {
                console.error('Error al cargar especialidades:', errorleer);
            }
        },

        async crear() {
            if (!this.validarCampos()) {
                console.error('Falta completar los campos del registro.');
                return;
            }

            try {
                const response = await this.post({
                    url: `${this.$store.getters.get__url}/specialty/store`,
                    token: this.$store.getters.get__token,
                    params: {
                        esp_nombre: this.especialidad.esp_nombre,
                    }
                });

                if (response.state === 0) {
                    await this.leer();

                    this.showModal = false;
                    this.especialidad.esp_id = "";
                    this.especialidad.esp_nombre = "";
                    console.info('especialidad creada correctamente');
                } else {
                    console.error('Error en la respuesta del servidor:', response.message);
                }
            } catch (error) {
                console.error('Error en la solicitud:', error);
            }
        },

        async actualizar(id) {
            if (!this.validarCampos()) {
                console.error('Falta completar los campos del registro.');
                return;
            }

            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/specialty/update",
                    token: this.$store.getters.get__token,
                    params: {
                        esp_id: id,
                        esp_nombre: this.especialidad.esp_nombre,
                    }
                });

                if (response.state == 0) {
                    await this.leer();
                    // await this.leer();
                    this.showModal = false;
                    this.especialidad.esp_id = "";
                    this.especialidad.esp_nombre = "";
                } else {
                    console.log("error");
                }
            } catch (errors) {
                console.error(errors);
            }
        },

        async borrar(id) {
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/specialty/destroy",
                    token: this.$store.getters.get__token,
                    params: {
                        esp_id: id
                    }
                });

                if (response.state == 0) {
                    await this.leer();;
                } else {
                    console.log("error");
                }
            } catch (errors) {
                console.error(errors);
            }
        },

        editar(id) {
            const editespecialidad = this.especialidades.find(especialidad => especialidad.esp_id === id);
            if (editespecialidad) {
                this.especialidad = { ...editespecialidad };
                this.showModal = true;
            } else {
                console.error("especialidad no encontrada");
            }
        },

        guardar() {
            try {
                if (!this.especialidad.esp_id) {
                    this.crear();
                } else {
                    this.actualizar(this.especialidad.esp_id);
                }
            } catch (error) {
                console.error(error);
            }
        },

        initDataTable() {
            if (this.dataTable) {
                this.dataTable.destroy();
            }

            this.$nextTick(() => {
                this.dataTable = $('#miTabla1').DataTable({
                    language: {
                        url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
                    },
                });
                feather.replace();
            });
        },

        mostrarModal() {
            this.showModal = true;
        },

        cerrarModal() {
            this.showModal = false;
            this.errores.esp_nombre = false;
            this.especialidad.esp_id = "";
            this.especialidad.esp_nombre = "";
        }
    },
};
</script>

<style scoped>
td {
    white-space: normal !important;
    word-wrap: break-word;
}

.circle {
    width: 30px;
    height: 30px;
    background-color: #7267EF;
    color: white;
    font-weight: 600;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: #fff;
    width: 500px;
    height: auto !important;
    /* padding: 20px; */
    border-radius: 8px !important;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    position: relative !important;
}

@media (max-width: 768px) {
    .modal-content {
        width: 90%;
    }
}

.card-header {
    border-radius: 8px 8px 0px 0px !important;
}

.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    padding-left: 15px;
    color: #EA4D4D;
    font-size: 12px;
}
</style>
